@use 'sass:color';
@use 'variables' as vars;

:root {
  --grid-columns: 6;
  --grid-margin: 1.875rem;
  --grid-gutter: 0.625rem;
  --scrollbar-compensation: 0px;
}

html {
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

html, body {
  height: 100%;
}

body {
  margin: 0;
  padding-top: vars.$header-height;
  font-family: vars.$font-base;
  background: vars.$color-surface;
  color: vars.$color-text;
  text-align: center;
  overflow-y: auto;
  display: grid;
  grid-template-columns: repeat(var(--grid-columns), minmax(0, 1fr));
  column-gap: var(--grid-gutter);
  row-gap: var(--grid-margin);
  padding-left: var(--grid-margin);
  padding-right: calc(var(--grid-margin) + var(--scrollbar-compensation));
  align-content: start;
  width: 100%;

  @media (min-aspect-ratio: 1/1) {
    --grid-columns: 12;
    --grid-margin: 2.5rem;
    --grid-gutter: 1.25rem;
    overflow: hidden;
    text-align: left;
    background: linear-gradient(
      to right,
      vars.$color-surface 0,
      vars.$color-surface calc(50% + (var(--scrollbar-compensation) / 2)),
      #ebf6f8 calc(50% + (var(--scrollbar-compensation) / 2)),
      #ebf6f8 100%
    );
  }
}

body.basket--open {
  overflow: hidden;
}

body > * {
  grid-column: 1 / -1;
}

.page {
  display: grid;
  grid-template-columns: repeat(var(--grid-columns), minmax(0, 1fr));
  column-gap: var(--grid-gutter);
  row-gap: var(--grid-margin);

  @media (min-aspect-ratio: 1/1) {
    height: calc(100vh - #{vars.$header-height});
    align-items: stretch;
    grid-auto-rows: 1fr;
  }
}

.page > * {
  grid-column: 1 / -1;
}

.header {
  width: 100%;
  left: 0;
  right: 0;
  top: 0;
  position: absolute;
  height: vars.$header-height;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: vars.$color-header;
  padding: 3.75rem 1.625rem 1.625rem 1.625rem;
  padding-right: calc(1.625rem + var(--scrollbar-compensation));

  &__logo {
    grid-column: 1 / span 2;
    justify-self: start;
  }

  &__basket-icon {
    grid-column: -2 / -1;
    justify-self: end;
    cursor: pointer;
    z-index: 2;
  }

  @media (min-aspect-ratio: 1/1) {
    padding: 2.5rem 2.5rem 2.5rem 2.5rem;
    background-color: transparent;
  }
}

.description {
  display: grid;
  grid-template-columns: repeat(var(--grid-columns), minmax(0, 1fr));
  row-gap: var(--grid-gutter);
  padding-block: 1.875rem;
  text-align: center;

  @media (min-aspect-ratio: 1/1) {
    grid-column: 1 / span 6;
    grid-row: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 2.5rem;
    padding: 5rem 3.5rem 5rem calc(var(--grid-margin));
  }

  &__header,
  &__body {
    grid-column: 1 / span 6;

    @media (min-aspect-ratio: 1/1) {
      margin: 0;
      max-width: 28.75rem;
    }
  }

  &__header {
    font-size: 2rem;
    font-weight: 400;

    @media (min-aspect-ratio: 1/1) {
      font-size: 2.5rem;
      line-height: 1.2;
    }
  }

  &__body {
    font-size: 1rem;
    font-weight: 400;
    font-family: vars.$font-secondary;
    line-height: 150%;

    @media (min-aspect-ratio: 1/1) {
      font-size: 1rem;
      line-height: 1.6;
    }
  }
}

.products {
  display: grid;
  grid-template-columns: repeat(var(--grid-columns), minmax(0, 1fr));
  column-gap: var(--grid-gutter);
  row-gap: var(--grid-margin);
  align-content: start;
  align-items: center;
  justify-items: center;
  padding-block: 3.83125rem;
  position: relative;

  &::-webkit-scrollbar {
    width: 8px;
  }

  &::-webkit-scrollbar-thumb {
    background: rgba(0,0,0,0.3);
    border-radius: 1rem;
  }

  &::before {
    content: '';
    position: absolute;
    inset: 0;
    left: calc(-1 * var(--grid-margin));
    right: calc(-1 * var(--grid-margin));
    background-color: #ebf6f8;
    z-index: -1;
    pointer-events: none;

    @media (min-aspect-ratio: 1/1) {
      display: none;
    }
  }

  @media (min-aspect-ratio: 1/1) {
    grid-column: 7 / span 6;
    grid-row: 1;
    display: flex;
    flex-direction: column;
    align-items: stretch;
    justify-content: flex-start;
    gap: var(--grid-margin);
    padding: 2rem calc(var(--grid-margin)) 5rem calc(var(--grid-margin));
    height: 100%;
    max-height: none;
    overflow-y: auto;
  }
}

.product {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 2.5rem 2.8125rem;
  background-color: vars.$color-surface;
  border-radius: 10px;
  box-shadow: 3px 4px 10px 10px rgba(194, 230, 237, 0.5);
  grid-column: 1 / span 6;
  justify-self: center;
  max-width: 21.125rem;

  @media (min-aspect-ratio: 1/1) {
    align-self: center;
    align-items: center;
    text-align: center;
  }

  &__image {
    width: 100%;
    height: auto;
  }

  &__title {
    font-weight: normal;
    font-size: 1.25rem;
  }

  &__price {
    color: vars.$color-accent;
    font-family: vars.$font-secondary;
    font-size: 1rem;
    line-height: 140%;
  }

  &__button {
    width: 11.1875rem;
    height: 2.9rem;
    border-radius: 3.125rem;
    background: vars.$color-accent;
    color: vars.$color-surface;
    border: none;
    cursor: pointer;
    transition: background 0.3s ease;
    font-size: 0.75rem;
    letter-spacing: 0.075rem;
    font-weight: 400;

    &:hover {
      background: color.adjust(vars.$color-accent, $lightness: -5%);
    }
  }
}

.basket {
  padding-top: 2.6875rem;
  position: fixed;
  top: 0;
  right: 0;
  width: 23rem;
  height: 100vh;
  background-color: vars.$color-surface;
  z-index: 20;
  box-shadow: -8px 0 24px rgba(0, 0, 0, 0.1);
  transform: translateX(100%);
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
  transition: transform 0.35s ease, opacity 0.35s ease;
  overflow: hidden;
  display: flex;
  flex-direction: column;

  &--active,
  &--closing {
    visibility: visible;
  }

  &--active {
    transform: translateX(0);
    opacity: 1;
    pointer-events: auto;
    visibility: visible;
  }

  &__overlay {
    position: fixed;
    inset: 0;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 10;
    opacity: 0;
    pointer-events: none;
    transition: opacity 0.3s ease;

    &--active {
      opacity: 1;
      pointer-events: auto;
    }
  }

  &__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1.625rem 1.5625rem 1rem 1.25rem;
    border-bottom: solid 1px #B8B8B8;
  }

  &__title {
    font-size: 1.5rem;
    font-style: normal;
    font-weight: 400;
    line-height: 2rem;
    margin: 0;
  }

  &__close {
    cursor: pointer;
    width: 0.875rem;
    height: 0.875rem;
    flex: 0 0 auto;
    display: inline-block;
  }

  &__body {
    padding: 1.5625rem 1.25rem;
    overflow-y: auto;
    flex: 1 1 auto;
    min-height: 0;
  }

  &__item {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    text-align: left;
    gap: 0.625rem;
    border-bottom: solid 1px #B8B8B8;
    padding: 1.25rem 0;
  }

  &__item-image {
    scale: 36vw;
    height: auto;
    width: 8.885rem;
    height: 7.6875rem;
  }

  &__item-details {
    h4 {
      margin: 0;
      margin-bottom: 0.3125rem;
    }
  }

  &__item-title {
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.5rem;
  }

  &__item-price {
    font-size: 1rem;
    font-weight: 700;
    line-height: 1.5rem;
    color: vars.$color-accent;
  }

  &__item-remove {
    font-size: 0.75rem;
    font-weight: 400;
    line-height: 1rem;
    color: #B8B8B8;
    cursor: pointer;
    text-decoration: underline;
    text-underline-offset: 0.2rem;
  }

  &__footer {
    width: 100%;
    padding: 1.625rem 1.25rem 3.12rem 1.25rem;
    border-top: solid 1px #B8B8B8;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: vars.$font-base;
    gap: 1.875rem;
    background-color: white;
    flex-shrink: 0;

    h4 {
      margin: 0;
    }
  }

  &__total {
    font-size: 1rem;
    font-weight: 500;
  }

  &__checkout {
    width: 13.5rem;
    height: 2.925rem;
    border-radius: 3.125rem;
    background: vars.$color-accent;
    color: vars.$color-surface;
    border: none;
    cursor: pointer;
    transition: background 0.3s ease;
    font-size: 0.75rem;
    letter-spacing: 0.075rem;
    font-weight: 400;

    &:hover {
      background: color.adjust(vars.$color-accent, $lightness: -5%);
    }
  }

  &__continue {
    color: var(--black-text, #000);
    text-align: center;
    font-family: Roboto;
    font-size: 0.75rem;
    font-style: normal;
    font-weight: 400;
    line-height: 1.25rem;
    letter-spacing: 0.075rem;
    text-decoration-line: underline;
    text-decoration-style: solid;
    text-decoration-skip-ink: none;
    text-decoration-thickness: 10%;
    text-underline-offset: 50%;
    text-underline-position: from-font;
    cursor: pointer;
  }
}